@import url(https://fonts.googleapis.com/css2?family=Gantari:ital,wght@0,200;0,600;1,400&display=swap);
.history-of-operation {
	text-align: right;
	flex: 1 1;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}

.number {
	background: #fff;
	box-shadow: 4px 4px 10px #dfdfe0;
	color: var(--text-color);
}

.wrapper--dark .number {
	background: #36383a;
	box-shadow: 4px 4px 10px #36383a;
}

.clear {
	background: url(data:image/png;base64,);
	background-color: #f2bb4e;
	background-position: center;
	background-size: 25%;
	background-repeat: no-repeat;
	box-shadow: 2px 2px 5px #f2bb4e;
	color: #fff;
}

.plus-minus {
	background: url(data:image/png;base64,);
}

.backspace {
	background: url(data:image/png;base64,);
	background-size: 40% !important;
}

.wrapper--dark .plus-minus, .wrapper--dark .backspace {
	background-color: #866240;
	box-shadow: 2px 2px 5px #A8916A;
}

.backspace,
.plus-minus {
	background-color: #f5f1e6;
	background-position: center;
	background-size: 30%;
	background-repeat: no-repeat;
	box-shadow: 2px 2px 5px #ceb781;
	color: #ceb781;
}

.divide {
	background: url(data:image/png;base64,);
	background-size: 30%;
}

.multiply {
	background: url(data:image/png;base64,);
	background-size: 30%;
}

.minus {
	background: url(data:image/png;base64,);
	background-size: 25%;
}

.plus {
	background: url(data:image/png;base64,);
	background-size: 25%;
}

.multiply,
.minus,
.plus,
.divide {
	background-color: #dfd5e7;
	background-position: center;
	background-repeat: no-repeat;
	box-shadow: 2px 2px 5px #683796;
	color: #683796;
}

.wrapper--dark .multiply,
.wrapper--dark .minus,
.wrapper--dark .plus,
.wrapper--dark .divide {
	background-color: #8f6cc4;
	box-shadow: 2px 2px 5px #7B36D2;
}

.result {
	background: url(data:image/png;base64,);
	background-size: 10%;
	background-color: #8531da;
	background-position: center;
	background-repeat: no-repeat;
	flex: 1 1;
	margin-right: 0 !important;
	border-radius: 50px !important;
	box-shadow: 2px 2px 5px #683796;
	color: #fff;
}

.list-of-buttons {
	display: flex;
	flex-wrap: wrap;
}

.result-field__value {
	font-weight: 600;
	font-size: 55px;
	width: 100%;
	text-align: right;
	color: var(--dark-text-color);
}

.wrapper--dark .result-field__value {
	background: #424547;
	color: var(--light-text-color);
}

@media screen and (max-width: 360px) {
	.result-field__value {
		font-size: 45px;
	}
}

.theme-switch {
	display: flex;
	align-items: center;
	padding: 15px 0;
}

.theme-switch__toggle {
	position: relative;
	display: inline-block;
	width: 42px;
	height: 25px;
	margin: 0;
	vertical-align: top;
	background: var(--light-text-color);
	border: 1px solid var(--light-text-color);
	border-radius: 30px;
	outline: none;
	cursor: pointer;
	-webkit-appearance: none;
	appearance: none;
	transition: all 0.3s cubic-bezier(0.2, 0.85, 0.32, 1.2);
}

.theme-switch__toggle::after {
	content: '';
	display: inline-block;
	position: absolute;
	left: 2px;
	top: 1.5px;
	width: 20px;
	height: 20px;
	background-color: #e4e7ea;
	border-radius: 50%;
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	transition: all 0.3s cubic-bezier(0.2, 0.85, 0.32, 1.2);
}

.theme-switch__toggle:checked::after {
	-webkit-transform: translateX(calc(100% - 5px));
	        transform: translateX(calc(100% - 5px));
	background-color: var(--dark-text-color);
}

.theme-switch__toggle:checked {
	background-color: #434547;
}

.theme-switch__title {
	margin-left: 10px;
	font-size: 16px;
}

.wrapper {
	width: 100%;
	height: 100%;
	background: #e4e7ea;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.wrapper--dark {
	background: #2b2e31;
	color: var(--light-text-color);
}

.container {
	width: 350px;
	height: 610px;
	background: #fff;
	border-radius: 15px;
	display: flex;
	flex-direction: column;
	padding: 15px 15px 0 15px;
	box-shadow: 0 0 25px #afb2b6;
}

.wrapper--dark .container {
	background: #424547;
	box-shadow: 0 0 25px #424547;
}

@media screen and (max-width: 360px) {
	.container {
		width: 280px;
		height: 520px;
	}
}

/** @format */

@import-normalize;

* {
	padding: 0;
	margin: 0;
	border: 0;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

:focus,
:active {
	outline: none;
}

html,
body {
	height: 100%;
	width: 100%;
	font-size: 14px;
	font-weight: 400;
	font-family: 'Gantari', sans-serif;
	color: #626872;
	color: var(--dark-text-color);
}

nav,
footer,
header,
aside {
	display: block;
}

a,
a:visited,
a:hover,
a:focus,
a:active {
	color: inherit;
	outline: none;
	text-decoration: none;
}

input,
button,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: inherit;
	font-weight: 400;
}

textarea {
	resize: none;
}

button {
	cursor: pointer;
}

ul li {
	list-style: none;
}

:root {
	--dark-text-color: #626872;
	--light-text-color: #a8aeae;
}

#root {
	width: 100%;
	height: 100%;
}

.calc-button {
	border-radius: 100%;
	font-size: 35px;
	padding: 10px;
	width: 70px;
	height: 70px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 13.3px;
	margin-bottom: 13.3px;
	font-weight: 200;
}

.calc-button:nth-child(4n + 4) {
	margin-right: 0;
}

.calc-button--clicked {
	opacity: 0.5;
}

@media screen and (max-width: 360px) {
	.calc-button {
		width: 52px;
		height: 52px;
	}
}

